<template>
    <div class="market t-font">
        <!-- <BgItem010 /> -->
        <a-row>
            <a-col>
                <a-table class="fontSize" :columns="columns" :dataSource="tableDatas" :loading="loading" :pagination="pagination" @change="handleTableChange" :rowKey="tableDatas => tableDatas.id">
                    <a slot="symbol" slot-scope="text, record">
                        <router-link :to="'/futures?symbol='+text+'usdt'">
                            <img :src="record.img" alt="image" height="24">
                            {{ text.toLocaleUpperCase() }}
                        </router-link>
                    </a>

                    <a slot="changePercent24Hr" slot-scope="text, record">
                        <span v-if="text>0" class="t-green">+{{text}}</span>
                        <span v-if="text<0" class="t-red">{{text}}</span>
                        <span v-if="text==0">{{text}}</span>
                        <span v-if="text==999">{{record}}</span>
                    </a>
                </a-table>
            </a-col>
        </a-row>

    </div>
</template>


<script>
import BgItem010 from "@/components/BgItem/Bg010.vue";

export default {
    name: "Market",
    components: {
        BgItem010
    },
    data() {
        return {
            loading: false,

            tableDatas: [],
            pagination: {
                total: 0,
                pageSize: 50,
            },
            queryParam: {
                page: 1,
                size: 50,
            },
            columns: [
                {
                    title: "Name",
                    dataIndex: "name",
                    width: "10%",
                    scopedSlots: { customRender: 'symbol' },
                }, {
                    title: "Last Price",
                    dataIndex: "priceUsd",
                    width: "15%",
                }, {
                    title: "Change",
                    dataIndex: "changePercent24Hr",
                    scopedSlots: { customRender: 'changePercent24Hr' },
                    width: "15%",
                }, {
                    title: "High",
                    dataIndex: "high",
                    width: "15%",
                }, {
                    title: "Low",
                    dataIndex: "low",
                    width: "15%",
                }, {
                    title: "24H Vol",
                    dataIndex: "volumeUsd24Hr",
                    width: "20%",
                    // scopedSlots: { customRender: 'status' },
                }
            ],
        };
    },
    destroyed: function () {
    },
    methods: {
        handleTableChange(pagination) {
            this.pagination.current = pagination.current;
            this.pagination.pageSize = pagination.pageSize;
            this.queryParam.page = pagination.current;
            this.queryParam.size = pagination.pageSize;
            // this.getTableList();
        },
        getClassName(item) {
            if (item.changePercent24Hr > 0) {
                return "up";
            } else if (item.changePercent24Hr < 0) {
                return "down";
            }
            return "none";
        },
        getPercent(item) {
            const resNum = item.close - item.open
            const p1 = resNum / item.close * 100
            return p1
        },
        showListHuobi(a) {
            let _t = this;
            let ts = Math.floor(new Date().getTime() / 1000 / 100) * 100;
            // let a = res.data.value;
            let i = 0
            a.forEach(function (item) {
                let name = item.name.toLocaleLowerCase();
                item._id = i++
                item.id = item.name
                item.class = _t.getClassName(item);
                item.changePercent24Hr = _t.g.round(_t.getPercent(item), 1, 4)
                item.priceUsd = item.close
                item.high = item.high
                item.low = item.low
                item.volumeUsd24Hr = _t.g.round(item.vol, 1, 4)
            });
            this.tableDatas = a;
        },
        initDataHuobi() {
            $.get(`${this.g.apiUrl}/api/caches?key=huobi-market-tickers`, (res) => {
                this.loading = false;
                if (res.flag != "ok") {
                    this.$message.error(res.flag)
                    return;
                }
                this.showListHuobi(res.data.value);
            }
            );
        },
        init() {
            // this.loading = true
            // this.initDataCoincap();
            // this.initDataHuobi()
            const arr = this.g.appWsData
            this.showListHuobi(arr)
            this.$EventBus.$on("appWsComplete", (arr) => {
                this.showListHuobi(arr)
            })
        },
    },
    mounted() {
        this.init();
    },
};
</script>



<style scoped>
.fontSize {
    font-size: 1rem;
    font-family: Verdana;
}
</style>